<template>
  <v-card
    width="100%"
    :height="$vuetify.breakpoint.mobile ? '100%' : 'fit-content'"
    class="user-card py-lg-5 py-md-12 pt-12 pb-7 mx-lg-2 mx-md-2 mx-5 px-lg-5 px-sm-12"
    :flat="$vuetify.breakpoint.mobile"
  >
    <div
      :class="[
        $vuetify.breakpoint.mobile ? 'd-flex flex-column justify-space-between' : '',
        'form-wrapper full-height'
      ]"
    >
      <v-form
        ref="form"
        v-model="form.valid"
        lazy-validation
        @keyup.enter.native="onSubmit"
      >
        <div>
          <base-field-input
            v-model="form.values.name"
            label="Full Name"
                          :is-required="true"
            :rules="form.nameRules"
          />

          <base-field-input
            v-model="form.values.email"
            label="Email"
                          :is-required="true"
            :rules="form.emailRules"
            :error="Boolean((serverErrors && serverErrors.email) || serverError)"
            :error-messages="serverErrors.email"
          />

          <base-field-input
            mask="+1 (###) ###-####"
            v-model="form.values.phone"
            label="Phone"
                          :is-required="true"
            :rules="form.phoneRules"
          />
        </div>

        <v-btn
          depressed
          :width="$vuetify.breakpoint.mobile ? '100%' : 100"
          :height="$vuetify.breakpoint.mobile ? 50 : 35"
          :disabled="!form.valid || !form.edited"
          color="primary"
          class="mt-2"
          :class="{ 'font-weight-bold': $vuetify.breakpoint.mobile }"
          @click="onSubmit"
        >
          Save
        </v-btn>
      </v-form>
    </div>
  </v-card>
</template>

<script>
import BaseFieldInput from '@components/BaseFieldInput';

import {
  required,
} from '@utils/helpers/validationRules';
import { createNamespacedHelpers } from 'vuex';
import * as constants from '@modules/dispatchers/constants';

const { mapGetters, mapActions } = createNamespacedHelpers('dispatchers');

export default {
  name: 'DispatcherEdit',
  watch: {
    'form.values': {
      deep: true,
      handler() {
        this.form.edited = !!this.getShallowDifferenceKeys(this.form.values, this.form.initialValues).length;
        this.serverError = '';
        Object.keys(this.serverErrors).forEach(
          (key) => {
            this.serverErrors[key] = '';
          },
        );
      },
    },
  },
  components: {
    BaseFieldInput,
  },
  data: () => ({
    form: {
      valid: true,
      initialValues: null,
      edited: false,
      values: {
        name: '',
        email: '',
        phone: '',
      },
      nameRules: [required('Full Name')],
      emailRules: [required('Email')],
      phoneRules: [required('Phone')],
    },
    serverErrors: {},
    serverError: '',
  }),
  computed: {
    ...mapGetters({
      activeDispatcher: constants.getters.ACTIVE_DISPATCHER,
    }),
  },
  methods: {
    onSubmit() {
      const isFormValid = this.$refs.form.validate();
      if (!isFormValid) {
        this.$notificationError('Form not valid');
        return;
      }

      const data = {};
      this.getShallowDifferenceKeys(this.form.values, this.form.initialValues).forEach((editedKey) => {
        data[editedKey] = this.form.values[editedKey];
      });

      this.editDispatcher({ id: this.activeDispatcher.id, data })
        .then(() => {
          this.form.edited = false;
          this.form.initialValues = { ...this.form.values };
          this.getDispatchers();
          this.$router.push({ name: this.$constants.ROUTE_NAMES.DISPATCHERS });
          this.$notificationSuccess('Dispatcher\'s information was updated');
        })
        .catch((errors) => {
          this.$notificationError('Dispatcher\'s information wasn\'t updated');
          if (Array.isArray(errors)) {
            this.serverError = errors;
          } else {
            this.serverErrors = errors;
          }
        });
    },
    getShallowDifferenceKeys(obj1, obj2) {
      return Object.keys(obj1)
        .filter((key) => obj2[key] !== obj1[key]);
    },
    ...mapActions({
      editDispatcher: constants.actions.EDIT_DISPATCHER,
      getDispatchers: constants.actions.GET_DISPATCHERS,
    }),
  },
  mounted() {
    if (this.activeDispatcher) {
      this.form.values = {
        name: this.activeDispatcher.name,
        email: this.activeDispatcher.email,
        phone: this.activeDispatcher.phone.startsWith('+1') ? this.activeDispatcher.phone.slice(2) : this.activeDispatcher.phone,
      };
    }

    this.form.initialValues = { ...this.form.values };
  },
};
</script>
